import React from "react";
import { useStaticQuery, graphql } from "gatsby";
import { useMappedImages } from "../../hooks/images";
import Img from "gatsby-image";

export default function Badges({ onGalleryImageClick }) {
  const data = useStaticQuery(graphql`
    query {
      allFile(
        filter: {
          relativePath: {
            in: [
              "projects/promotional-assets/blackfriday_badge1.png"
              "projects/promotional-assets/blackfriday_badge2.png"
              "projects/promotional-assets/blackfriday_badge3.png"
              "projects/promotional-assets/blackfriday_badge4.png"
              "projects/promotional-assets/blackfriday_badge5.png"
              "projects/promotional-assets/blackfriday_badge6.png"
            ]
          }
        }
      ) {
        ...FluidImagesFragment
      }
    }
  `);
  const optimizedImages = useMappedImages(data.allFile);

  return (
    <div class="mt-6">
      <p>Product badges</p>
      <div class="smooth-scrool overflow-x-scroll flex md:justify-center p-2 md:p-0 bg-gray-100 md:bg-transparent border md:border-none border-gray-200 md:border-transparent rounded-full">
        <div
          class="mr-4 w-24 flex-shrink-0"
          onClick={() => {
            onGalleryImageClick(
              "projects/promotional-assets/blackfriday_badge1.png"
            );
          }}
        >
          <Img
            className={"cursor-zoom-in"}
            fluid={
              optimizedImages[
                "projects/promotional-assets/blackfriday_badge1.png"
              ].childImageSharp.fluid
            }
          />
        </div>
        <div
          class="mx-4 w-24 flex-shrink-0"
          onClick={() => {
            onGalleryImageClick(
              "projects/promotional-assets/blackfriday_badge2.png"
            );
          }}
        >
          <Img
            className={"cursor-zoom-in"}
            fluid={
              optimizedImages[
                "projects/promotional-assets/blackfriday_badge2.png"
              ].childImageSharp.fluid
            }
          />
        </div>
        <div
          class="mx-4 w-24 flex-shrink-0"
          onClick={() => {
            onGalleryImageClick(
              "projects/promotional-assets/blackfriday_badge3.png"
            );
          }}
        >
          <Img
            className={"cursor-zoom-in"}
            fluid={
              optimizedImages[
                "projects/promotional-assets/blackfriday_badge3.png"
              ].childImageSharp.fluid
            }
          />
        </div>
        <div
          class="mx-4 w-24 flex-shrink-0"
          onClick={() => {
            onGalleryImageClick(
              "projects/promotional-assets/blackfriday_badge4.png"
            );
          }}
        >
          <Img
            className={"cursor-zoom-in"}
            fluid={
              optimizedImages[
                "projects/promotional-assets/blackfriday_badge4.png"
              ].childImageSharp.fluid
            }
          />
        </div>
        <div
          class="mx-4 w-24 flex-shrink-0"
          onClick={() => {
            onGalleryImageClick(
              "projects/promotional-assets/blackfriday_badge5.png"
            );
          }}
        >
          <Img
            className={"cursor-zoom-in"}
            fluid={
              optimizedImages[
                "projects/promotional-assets/blackfriday_badge5.png"
              ].childImageSharp.fluid
            }
          />
        </div>
        <div
          class="ml-4 w-24 flex-shrink-0"
          onClick={() => {
            onGalleryImageClick(
              "projects/promotional-assets/blackfriday_badge6.png"
            );
          }}
        >
          <Img
            className={"cursor-zoom-in"}
            fluid={
              optimizedImages[
                "projects/promotional-assets/blackfriday_badge6.png"
              ].childImageSharp.fluid
            }
          />
        </div>
      </div>
    </div>
  );
}
